import baseURL from '../../config/index.js'
export default {
	onLoad(options) {
		// this.initGetQRCode()
		this.QRUrl = options.QRUrl
	},
	data () {
		return {
			qrcode: '',
			avatar: '',
			userName: '',
			RichText: [],
			inviteCode: '',
			sharUrl:'',
			QRUrl: ''
		}
	},
	methods: {
		goToBack () {
			uni.navigateBack({
				delta:1
			})
		},
		// 初始化二维码
		// async initGetQRCode () {
		// 	let res = await this.$fetch(this.$api.getQrCode,{},'GET','FORM')
		// 	console.log(res.data)
		// 	this.QRUrl = baseURL +　res.data.slice(1)
		// 	console.log(this.QRUrl)
		// },
		//保存图片
		saveImage(){
			uni.authorize({
			    scope: 'scope.writePhotosAlbum',
			    success:() => {
			       uni.downloadFile({
			           url: this.QRUrl, //仅为示例，并非真实的资源
			           success: (res) => {
			               if (res.statusCode === 200) {
			       			uni.saveImageToPhotosAlbum({
			       				filePath: res.tempFilePath,
			       				success: function() {
			       					uni.showToast({
			       						icon: "success",
			       						title: "保存成功"
			       						
			       					});
			       				},
			       				fail: function() {
			       					uni.showToast({
			       						title: "保存失败，请稍后重试",
			       						icon: "none"
			       					});
			       				}
			       			});
			         
			               }
			           },
					   fail:() => {
					   	uni.showModal({
					   		title: '授权提示',
							content: '暂未授权, 是否授权',
							showCancel: false,
							success: res => {
								uni.openSetting()
							}
					   	})
					   }
			       });
			    }
			})
			
			
		}
	}
}
